import NavbarConfig from "../../InstituteMember/Config/NavbarConfig";
import { useLocation, useSearchParams } from "react-router-dom";
import { useTranslation } from "react-i18next";
import GoBack from "../../Shared/StaffStudentProfile/GoBack";
import StaffStudentProfileContainer from "../../Shared/StaffStudentProfile/StaffStudentProfileContainer";
import style from "../FinanceManager/FinanceManager.module.css";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../Ui/Border/BorderBottom";
import CreateButton from "../../Ui/Button/CreateButton";
import { useState } from "react";
import StudentEnrollmentTable from "./StudentEnrollmentTable";
import SelectInstituteUniversalBatch from "../SelectInstituteUniversalBatch/SelectInstituteUniversalBatch";
import {
  useFinanceGetStudentEnrollment,
  useFinanceStudentEnrollment,
  useUserFeedOtherInfo,
} from "../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import QvipleLoader from "../../Loader/QvipleLoader";
import { useSelector } from "react-redux";
import Switch from "@mui/material/Switch";
import StudentStatiticsDetail from "../FinanceManager/StudentStatitics/StudentStatiticsDetail";
import StudentStatitics from "../FinanceManager/StudentStatitics/StudentStatitics";
const label = { inputProps: { "aria-label": "Switch demo" } };

const StudentEnrollment = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  const [step, setStep] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [toggle, setToggle] = useState(true);

  const uid = useSelector((state) => state.idChange.id);

  const [financeStudentEnrollment] = useFinanceStudentEnrollment();
  const { userFeedOtherInfo } = useUserFeedOtherInfo({
    data: {
      uid: uid,
    },
    skip: !uid,
  });
  const { financeGetStudentEnrollment, financeGetStudentEnrollmentLoading } =
    useFinanceGetStudentEnrollment({
      data: {
        aid: userFeedOtherInfo?.institute?.admissionDepart?.[0],
      },
      skip: !userFeedOtherInfo?.institute?.admissionDepart?.[0],
    });

  const onClose = () => {
    setStep("");
  };

  const onSelectUniversalBatch = (dt) => {
    if (dt?._id && userFeedOtherInfo?.institute?.admissionDepart?.[0]) {
      setDisabled((pre) => !pre);
      financeStudentEnrollment({
        bid: dt?._id,
        aid: userFeedOtherInfo?.institute?.admissionDepart?.[0],
      })
        .then(() => {
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  // console.info("financeGetStudentEnrollment", financeGetStudentEnrollment);
  return (
    <>
      <NavbarConfig
        isView={
          getQuery.state?.viewAs === "INSTITUTE"
            ? "INSTITUTE"
            : getQuery.state?.whichUrl === "INSTITUTE"
            ? "INSTITUTE"
            : getQuery.state?.viewAs
        }
      >
        <GoBack />
        {toggle ? (
          <StaffStudentProfileContainer>
            <StudentStatitics
              isDashboard
              onDashClose={() => setToggle(false)}
              fid={userFeedOtherInfo?.institute?.financeDepart?.[0]}
              aid={userFeedOtherInfo?.institute?.admissionDepart?.[0]}
              instituteId={userFeedOtherInfo?.institute?._id}
              toggle={toggle}
            />
          </StaffStudentProfileContainer>
        ) : (
          <StaffStudentProfileContainer>
            {searchParams.get("type") === "statistics-detail" ? (
              <StudentStatiticsDetail />
            ) : (
              <>
                <UserMemberContentWrapper>
                  <div className={style.fm_header_container}>
                    <h6>{t("student_enrollment")}</h6>
                    <div
                      className={style.fm_header_container}
                      style={{
                        gap: "1rem",
                      }}
                    >
                      <div
                        style={{
                          marginTop: "0.7rem",
                        }}
                      >
                        <Switch
                          {...label}
                          checked={toggle}
                          onChange={(e) => setToggle(e.target.checked)}
                        />
                        <span>{t("normal_department")}</span>
                      </div>

                      <CreateButton
                        label="select_universal_batch"
                        customStyle={{
                          margin: "0",
                          padding: "0.3rem 1rem",
                        }}
                        onAction={() => setStep(1)}
                      />
                    </div>
                  </div>
                </UserMemberContentWrapper>
                <BorderBottom />
                <UserMemberContentWrapper
                  customStyle={{
                    padding: "0",
                    width: "1128px",
                  }}
                >
                  <StudentEnrollmentTable
                    // tableData={state}
                    tableData={financeGetStudentEnrollment}
                  />
                </UserMemberContentWrapper>
              </>
            )}
          </StaffStudentProfileContainer>
        )}
      </NavbarConfig>
      {step === 1 ? (
        <SelectInstituteUniversalBatch
          onSelectStaff={onSelectUniversalBatch}
          onClose={onClose}
          instituteId={userFeedOtherInfo?.institute?._id}
        />
      ) : null}
      {financeGetStudentEnrollmentLoading && <QvipleLoader />}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default StudentEnrollment;
