import { useTranslation } from "react-i18next";
import style from "../FinanceManager/StudentStatitics/StudentStatitics.module.css";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import { useCallback, useEffect, useState } from "react";
import { assestsNavbarUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import styleFy from "../FinanceManager/FinanceManager.module.css";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";

const StudentEnrollmentTable = ({ tableData }) => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const params = useParams();
  const getQuery = useLocation();
  const [state, setState] = useState(tableData ?? []);
  const [search, setSearch] = useState("");

  useEffect(() => {
    if (tableData?.length > 0) {
      setState(tableData);
    }
  }, [tableData]);
  const onNavigate = useCallback(
    (val) => {
      if (val?.length > 0) {
        let url = `/q/${params.username}/member/staff/student/enrollment?type=statistics-detail`;
        navigate(url, {
          state: {
            ...getQuery.state,
            studentList: val,
          },
        });
      }
    },
    [navigate, params, getQuery.state]
  );

  const onSearchEvent = (e) => {
    if (tableData?.length >= 0) {
      setSearch(e.target.value);
      let ft = tableData?.filter((val) => {
        if (
          val?.DepartmentName?.toLowerCase()?.includes(
            e.target.value?.toLowerCase()
          )
        ) {
          return val;
        } else {
          return null;
        }
      });
      setState(ft);
    }
  };

  return (
    <>
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
          marginBottom: "0.7rem",
        }}
      >
        <div className={styleFy.fm_search_full}>
          <section className={styleFy.fm_search_full_container}>
            <div className={styleFy.fm_search_full_container_inner}>
              <img
                className={styleFy.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
                value={search}
              />
            </div>
          </section>
        </div>
      </UserMemberContentWrapper>
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        {state?.map((data, index) => (
          <section
            className={style.set_main}
            key={`${data?.DepartmentName}${index}`}
            style={{
              marginBottom: "2.5rem",
            }}
          >
            <div className={style.set_section}>
              <div className={style.sets_c}>
                <h6
                  className={style.set_dp_heading}
                  style={{
                    height: "100px",
                  }}
                >
                  {data?.DepartmentName ?? ""} - {data?.Batch ?? ""}
                </h6>
              </div>
              <div
                className={style.sets_c}
                style={{
                  height: "100px",
                }}
              >
                <h6 className={style.set_dp_heading}>Regular</h6>
                <div
                  className={style.setsc_r}
                  style={{
                    height: "104%",
                  }}
                >
                  <div className={style.setscr_f}>
                    <h6 className={style.set_heading}>Male</h6>
                  </div>
                  <div className={style.setscr_f}>
                    <h6 className={style.set_heading}>Female</h6>
                  </div>
                  <div className={style.setscr_f}>
                    <h6 className={style.set_heading}>Total</h6>
                  </div>
                </div>
              </div>
              <div
                className={style.sets_c}
                style={{
                  height: "100px",
                }}
              >
                <h6 className={style.set_dp_heading}>DSE</h6>
                <div
                  className={style.setsc_r}
                  style={{
                    height: "104%",
                  }}
                >
                  <div className={style.setscr_f}>
                    <h6 className={style.set_heading}>Male</h6>
                  </div>
                  <div className={style.setscr_f}>
                    <h6 className={style.set_heading}>Female</h6>
                  </div>
                  <div className={style.setscr_f}>
                    <h6 className={style.set_heading}>Total</h6>
                  </div>
                </div>
              </div>
              <div
                className={style.sets_c}
                style={{
                  height: "101px",
                  borderBottom: "1px solid #121212",
                }}
              >
                <h6 className={style.set_dp_heading}>Total</h6>
              </div>
            </div>
            <div className={style.set_section}>
              <div className={style.setsc_n}>
                {data?.master?.map((d_data, dindex) => (
                  <h6
                    className={`${style.setscn_y} ${style.set_heading}`}
                    key={`${d_data}${dindex}`}
                  >
                    {d_data ?? ""}
                  </h6>
                ))}
              </div>
              <div className={style.setsc_r}>
                <div className={style.setscr_op}>
                  {data?.regular?.male?.map((gt, int) => (
                    <div className={style.setscn_yt} key={`${gt}${int}`}>
                      <h6
                        className={style.set_heading}
                        onClick={() => onNavigate(gt?.count_bind)}
                        style={{
                          color: "#00f",
                          cursor: "pointer",
                        }}
                      >
                        {gt?.count}
                      </h6>
                    </div>
                  ))}
                </div>

                <div className={style.setscr_op}>
                  {data?.regular?.female?.map((gt, int) => (
                    <div className={style.setscn_yt} key={`${gt}${int}`}>
                      <h6
                        className={style.set_heading}
                        onClick={() => onNavigate(gt?.count_bind)}
                        style={{
                          color: "#00f",
                          cursor: "pointer",
                        }}
                      >
                        {gt?.count}
                      </h6>
                    </div>
                  ))}
                </div>

                <div className={style.setscr_op}>
                  {data?.regular?.total?.map((gt, int) => (
                    <div className={style.setscn_yt} key={`${gt}${int}`}>
                      <h6
                        className={style.set_heading}
                        onClick={() => onNavigate(gt?.count_bind)}
                        style={{
                          color: "#00f",
                          cursor: "pointer",
                        }}
                      >
                        {gt?.count}
                      </h6>
                    </div>
                  ))}
                </div>
              </div>
              <div className={style.setsc_r}>
                <div className={style.setscr_op}>
                  {data?.dse?.male?.map((gt, int) => (
                    <div className={style.setscn_yt} key={`${gt}${int}`}>
                      <h6
                        className={style.set_heading}
                        onClick={() => onNavigate(gt?.count_bind)}
                        style={{
                          color: "#00f",
                          cursor: "pointer",
                        }}
                      >
                        {gt?.count}
                      </h6>
                    </div>
                  ))}
                </div>

                <div className={style.setscr_op}>
                  {data?.dse?.female?.map((gt, int) => (
                    <div className={style.setscn_yt} key={`${gt}${int}`}>
                      <h6
                        className={style.set_heading}
                        onClick={() => onNavigate(gt?.count_bind)}
                        style={{
                          color: "#00f",
                          cursor: "pointer",
                        }}
                      >
                        {gt?.count}
                      </h6>
                    </div>
                  ))}
                </div>

                <div className={style.setscr_op}>
                  {data?.dse?.total?.map((gt, int) => (
                    <div className={style.setscn_yt} key={`${gt}${int}`}>
                      <h6
                        className={style.set_heading}
                        onClick={() => onNavigate(gt?.count_bind)}
                        style={{
                          color: "#00f",
                          cursor: "pointer",
                        }}
                      >
                        {gt?.count}
                      </h6>
                    </div>
                  ))}
                </div>
              </div>

              <div className={style.setsc_n}>
                {data?.regular?.total?.map((d_data, dindex) => (
                  <h6
                    className={`${style.settt_y} ${style.set_heading}`}
                    key={`${d_data}${dindex}`}
                    onClick={() => onNavigate(d_data?.count_bind)}
                    style={{
                      color: "#00f",
                      cursor: "pointer",
                    }}
                  >
                    {d_data?.count ?? ""}
                  </h6>
                ))}
              </div>
            </div>
          </section>
        ))}
      </UserMemberContentWrapper>
    </>
  );
};

export default StudentEnrollmentTable;
